"use client";

import { motion } from "framer-motion";
import { Briefcase, GraduationCap, Calendar } from "lucide-react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";

const experienceData = [
    {
        type: "education",
        title: "B.E. in Computer Science",
        organization: "Chandigarh University",
        period: "2023 - 2027",
        description: "Bachelor of Engineering in Computer Science. CGPA: 7.49/10.",
    },
    {
        type: "education",
        title: "12th",
        organization: "Shree Ln Academy Kherod Dhar, Madhya Pradesh",
        period: "2021 - 2022",
        description: "Higher Secondary Education. Percentage: 83%.",
    },
    {
        type: "education",
        title: "10th",
        organization: "Shree Ln Academy Kherod Dhar, Madhya Pradesh",
        period: "2019 - 2020",
        description: "Secondary Education. Percentage: 90.33%.",
    },
];

export function Experience() {
    return (
        <section id="education" className="py-20 px-4 sm:px-6 lg:px-8 bg-background">
            <div className="max-w-4xl mx-auto">
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    whileInView={{ opacity: 1, y: 0 }}
                    viewport={{ once: true }}
                    transition={{ duration: 0.5 }}
                    className="text-center mb-16"
                >
                    <h2 className="text-3xl md:text-4xl font-bold mb-4"> Education</h2>
                    <div className="w-20 h-1 bg-primary mx-auto rounded-full" />
                </motion.div>

                <div className="space-y-8">
                    {experienceData.map((item, index) => (
                        <motion.div
                            key={index}
                            initial={{ opacity: 0, x: index % 2 === 0 ? -20 : 20 }}
                            whileInView={{ opacity: 1, x: 0 }}
                            viewport={{ once: true }}
                            transition={{ duration: 0.5, delay: index * 0.1 }}
                        >
                            <Card className="border-l-4 border-l-primary shadow-sm hover:shadow-md transition-shadow">
                                <CardHeader className="flex flex-row items-center justify-between pb-2">
                                    <div className="flex items-center gap-3">
                                        <div className="p-2 bg-secondary rounded-full">
                                            {item.type === 'work' ? (
                                                <Briefcase className="w-5 h-5 text-primary" />
                                            ) : (
                                                <GraduationCap className="w-5 h-5 text-primary" />
                                            )}
                                        </div>
                                        <div>
                                            <CardTitle className="text-lg font-bold">{item.title}</CardTitle>
                                            <p className="text-sm font-medium text-muted-foreground">{item.organization}</p>
                                        </div>
                                    </div>
                                    <div className="flex items-center gap-1 text-sm text-muted-foreground whitespace-nowrap hidden sm:flex">
                                        <Calendar className="w-4 h-4" />
                                        {item.period}
                                    </div>
                                </CardHeader>
                                <CardContent>
                                    <div className="sm:hidden text-sm text-muted-foreground mb-2 flex items-center gap-1">
                                        <Calendar className="w-4 h-4" />
                                        {item.period}
                                    </div>
                                    <p className="text-muted-foreground">{item.description}</p>
                                </CardContent>
                            </Card>
                        </motion.div>
                    ))}
                </div>
            </div>
        </section>
    );
}
